.Giverating-container{
  position: fixed;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
}

.box{
    position: relative;
    width: 50%;
    margin: 0 auto;
    height: auto;
    max-height: 70vh;
    margin-top: calc(100vh - 85vh - 20px);
    background: #ffe1e5;
    color: black;
    border-radius: 10px;
    padding: 20px;
    border: 1px solid #999;
    overflow: auto;
}
.heading-rating{
  padding-bottom: 20px;
}

.mb-3{
  text-align: left;
  font-size: 15px;
  font-family: "Noto Sans", sans-serif;

}
.close-icon {
    content: 'x';
    cursor: pointer;
    position: fixed;
    right: calc(15% - 30px);
    top: calc(100vh - 85vh - 33px);
    background: #ededed;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    line-height: 20px;
    text-align: center;
    border: 1px solid #999;
    font-size: 20px;
  }